import React from 'react'
import {Formik , Form, Field, FieldArray} from 'formik'
import axios from 'axios'
import * as Yup from 'yup'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'jquery/dist/jquery.min.js'
import 'bootstrap/dist/js/bootstrap.min.js'

const initialValues = {
    submittedBy : '',
    subject : '',
    chapter : '',
    questions : [
        { choices : ['','','',''] }
    ]
}
const  onSubmit = values  =>{
    console.log('Form data : ', values)
    addQuestions(values)
}

async function addQuestions (data){
    console.log(data)
    console.log("popo")
    const config = {
      headers:{
        'Content-type':'application/json  '
      }
    }
    try {
      const res =  await axios.post('/newmcqs/test',data,config)
    } catch (err) {
      console.log("error here : ",err)
    }
    
  }
 

const validationSchema = Yup.object({
    submittedBy : Yup.string().required('submittedBy required'),
    subject : Yup.string().required('subject required'),
    chapter : Yup.string().required('Channeeel name required')

})

function Youtube() {

    // console.log('Visited',formik.touched)
    return (
        <Formik initialValues={initialValues}  onSubmit={onSubmit}>
            <Form className = 'mt-4' action="/newmcqs/test" method="POST">
                <div className="input-group mb-3 mt-4 mr-4 col-md-6">
                    <div className="input-group-prepend">
                        <label className="input-group-text" htmlFor="inputGroupSelect01">Subject</label>
                    </div>
                    <Field as='select' name="subject" className="custom-select" id="inputGroupSelect01" required>
                        <option defaultValue>Choose...</option>
                        <option value="math">Math</option>
                        <option value="physics">Physics</option>
                        <option value="chemistry">Chemistry</option>
                        <option value="biology">biology</option>
                        <option value="english">English</option>
                        <option value="intelligence">Intelligence</option>
                    </Field>
                </div>
                
                <div className="input-group mb-3 ml-3 mr-4">
                    <div className="input-group-prepend">
                        <label className="input-group-text" htmlFor="inputGroupSelect01">Chapter</label>
                    </div>
                    <Field  name="chapter"  className="input-group-text" id="inputGroupSelect01" required>
                    </Field>
                </div>
                <div className='card mt-3 mb-3'>
                    <div className='card-body'>
                        <div className='form-controls'>
                        <FieldArray name='questions'>
                            {
                                (fieldArrayProps)=>{
                                    console.log('questions field array',fieldArrayProps)
                                    const {push,remove,form} = fieldArrayProps
                                    const {questions} = form.values
                                    return (
                                        <div>
                                            {questions.map((question , index)=>
                                                <div className='card mt-3 mb-3' key={index}>
                                                    <div className='card-header'>
                                                        <h1>Question : {index + 1}</h1>
                                                        {index > -1 ? <button className='btn btn-danger float-right float-bottom btn-sm ml-3' type='button' onClick={()=>remove(index)}> <svg className="bi bi-trash" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                                                            <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                                                        </svg> </button> : null}
                                                        <div className="bs-example">
                                                            {/* <Field type="text" name={`questions[${index}].tags`} placeholder='tags'  data-role="tagsinput" /> */}
                                                        </div>
                                                    </div>
                                                    <div className='card-body'>
                                                        <div className=" input-group-prepend col-md-12 mb-3 mt-3 "  >
                                                            <label className="input-group-text " htmlFor="question">Question</label>
                                                            <Field as='textarea' rows="4" cols="68" name={`questions[${index}].question`} className="form-control " placeholder='Type questions here ...' required />
                                                        </div>
                                                        <FieldArray name={`questions[${index}].choices`}>
                                                            {
                                                                (fieldArrayProps)=>{
                                                                    console.log('choices field array',fieldArrayProps)
                                                                    const {push,remove,form} = fieldArrayProps
                                                                    const {questions} = form.values
                                                                    console.log('question : ',questions)
                                                                    return(
                                                                        <div>
                                                                            <div className='row' >
                                                                                {questions[index].choices.map((choice,choiceIndex)=>
                                                                                    <div key={choiceIndex}  className=" input-group-prepend col-md-6 mb-3 mt-3 choice" >
                                                                                        <label className="input-group-text" htmlFor="choice1">{`Choice ${choiceIndex + 1}`}</label>
                                                                                        <Field as='textarea' cols="35" className="form-control " name={`questions[${index}].choices[${choiceIndex}]`} placeholder={`Choice ${choiceIndex + 1}`} required />
                                                                                        <button className='btn btn-danger btn-sm ml-4' type='button' onClick={()=>remove(choiceIndex)}> <svg className="bi bi-trash" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/><path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/></svg> </button>
                                                                                    </div>
                                                                                )}
                                                                            </div>
                                                                            <div className="input-group-prepend mt-3">
                                                                                <label className="input-group-text" htmlFor="inputGroupSelect01">Add Choice</label>
                                                                                <button type='button'  className='btn btn-success btn-sm'  onClick={()=>push('')}> + </button>
                                                                                
                                                                            </div>
                                                                        </div>
                                                                    )
                                                                }
                                                            }
                                                        </FieldArray>
                                                    </div>
                                                    <div className='card-footer'>
                                                        <div className="input-group col-md-3 mb-3 mr-4">
                                                            <div className="input-group-prepend">
                                                                <label className="input-group-text" htmlFor="inputGroupSelect01">Correct</label>
                                                            </div>
                                                            <Field as='select' name={`questions[${index}].answer`} className="custom-select" id="inputGroupSelect01" required>
                                                                {questions[index].choices.map((choice,i)=>
                                                                    <option key={i+100}  value={` ${i + 1}`} defaultValue >{` ${i + 1}`}</option>
                                                                )}
                                                            </Field>
                                                        </div>
                                                        <div className="form-label col-md-5 mb-3">
                                                            <label className="input-group-text" htmlFor="solution">Solution explanation </label>
                                                            <Field as='textarea' cols="55" row='4' className='form-control'  name={`questions[${index}].solution`} placeholder='solution'  />                                                       
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                            <div className="input-group-prepend mt-3">
                                                <label className="input-group-text" htmlFor="inputGroupSelect01">Add question</label>
                                                <button type='button'  className='btn btn-success btn-sm '  onClick={()=>push( { choices : ['','','',''] })}> + </button>
                                            </div>
                                        </div>
                                    )
                                }
                            }
                        </FieldArray>
                    </div>
                    </div>
                </div>
                <button type='submit' className='btn btn-primary mb-5'>Submit</button>
            </Form>
        </Formik>
    )
}

export default Youtube
